import { Controller } from '@ali/4ever-cangjie';

export default function isUnorderedList(editor: Controller) {
  const { value } = editor;
  const { selection, document } = value;
  const block =
    selection &&
    document.getClosest(
      selection.focus.key,
      ({ type }) =>
        type === 'paragraph' || String(type).indexOf('heading-') >= 0,
    );
  return Boolean(block && block.data.list && !block.data.list.isOrdered);
}
